.categories {
    margin-right: 111px;
    margin-top: 28px;

    &_ul {
        width: fit-content;
        display: flex;
        align-items: center;
        margin-left: 16px;

        @media screen and (min-width: $tablet) {
            margin-left: 16px;
            column-gap: 16px;
        }
    }

    &_button {
        display: flex;
        align-items: center;
        padding: 8px 24px;
        border: 1px solid $color-text-link;
        color: $color-text-link;
        border-radius: 20px;
        background-color: $color-background;
        font-size: 12px;
        line-height: 1.3;
        transition: all 250ms;
        cursor: pointer;

        &_button:nth-of-type(odd) {
            background: cadetblue;
        }
    }
}

.special_but {
    position: relative;
    width: 112px;
}

.chanhe-back {
    background-color: $color-button-hover;
    color: $color-background;
}

.icon {
    position: absolute;
    left: 72px;
    fill: $color-button-hover;
    top: 9px;
    transition: all 250ms;
}

.icon-click {
    fill: $color-background;
    transform: rotate(180deg);
}

.hide-but_mob {
    @media screen and (max-width: 767px) {
        display: none;
    }
}

.hide-but_tablet {
    @media screen and (max-width: 1279px) {
        display: none;
    }
}

.other_li {
    position: relative;
}

.special {
    position: absolute;
    left: 0;
    top: 32px;
    background-color: $color-background-categories;
    display: none;
    width: 148px;
    height: 380px;
    border-radius: 20px;
    padding: 14px 14px 0 14px;
    transition: transform 250ms ease-in-out;
    transition: all 250ms;
    overflow: hidden;
    z-index: 2;
}

.other_but {
    padding-bottom: 12px;
    background-color: transparent;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.01em;
    transition: all 250ms;

    color: $color-button-hover;
}

.other_but:hover {
    text-decoration: underline;
}

.is-hidden {
    display: block;
}

.addforbut {
    @media screen and (max-width: 767px) {
        display: none;
    }
}

.display_button {
    @media screen and (max-width: 767px) {
        display: none;
    }
}

.click-chang-bac {
    background-color: $color-button-hover;
    color: $color-background;
}

.display_buttons {
    display: flex;
    column-gap: 16px;
}

.bek-for-other {
    background-color: $color-button-hover;
    color: $color-background;
}
