}

.modal-link__pointer {
  display: none;
}

.modal__item:hover .modal-link__pointer, .modal__item:focus .modal-link__pointer {
  display: block;
}

.modal__theme-switcher {
  margin-left: 16px;
}

body.light-theme {
  color: #111321;
  background-color: #f4f4f4;
}

body.light-theme .theme-switch__icon--light {
  fill: #4440f7;
}

body.light-theme .theme-switch__text--light {
  color: #4440f7;
}

body.dark-theme {
  color: #f4f4f4;
  background-color: #2e2e2e;
}

body.dark-theme .theme-switch__icon--dark {
  fill: #4440f7;
}

body.dark-theme .theme-switch__text--dark {
  color: #4440f7;
}

body.dark-theme .theme-switch__control {
  border-color: #f4f4f4;
}

body.dark-theme .theme-switch__track {
  background-color: #4440f7;
}

body.dark-theme .theme-switch__marker {
