}

.theme-switch {
  align-items: center;
  margin-left: auto;
  display: flex;
}

.theme-switch__icon {
  fill: #a8a8a8;
  height: 21px;
  width: 21px;
  display: block;
}

@media screen and (min-width: 1280px) {
  .theme-switch__icon {
    display: none;
  }
}

.theme-switch__text {
  color: #a8a8a8;
  font-size: 18px;
  line-height: 1.33;
  display: none;
}

@media screen and (min-width: 1280px) {
  .theme-switch__text {
    display: block;
  }
}

.theme-switch__control {
  width: 40px;
  height: 20px;
  border: 1px solid #4440f7;
  border-radius: 20px;
  align-items: center;
  margin-left: 8px;
  margin-right: 8px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 1280px) {
  .theme-switch__control {
    margin-left: 12px;
    margin-right: 12px;
  }
}

.theme-switch__toggle {
  height: 0;
  width: 0;
  position: absolute;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  background-color: inherit;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 16px;
  height: 16px;
  z-index: 2;
  pointer-events: none;
  background-color: #4440f7;
  border-radius: 50%;
  transform: translateX(1px);
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(21px);
}

